import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
    {
        path: 'registerChargerRequest',
        loadChildren: () => import('./register-charger-request/register-charger-request.module').then(m => m.RegisterChargerRequestModule),
        data: { title: 'Register Charger Request', titleI18n: 'RegisterChargerRequest' },
    },
    {
        path: 'newEvStationRequest',
        loadChildren: () => import('./EV-charging-station-request/EV-charging-station-request.module').then(m => m.EVChargingStationRequestModule),
        data: { title: 'EV Charging Station Request', titleI18n: 'EV Charging Station Request' },
    },
]

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})

export class CustomerManagementRoutingModule { }